@media (min-width: 768px) {
    .container {
      max-width: 720px;
    }
    .flexContainer {
      display: flex;
    }
    .spaceBetween {
      justify-content: space-between;
    }
  }

  @media (min-width: 768px) {
    ul.clubs.gridContainer {
      display: grid;
      grid-template-columns: repeat(auto-fit, 50%);
    }
  }

  
@media (min-width: 768px) {
    .content ul {
      display: flex;
      justify-content: space-between;
    }
    .content ul li {
      width: 30%;
    }
    .intro p {
      width: 80%;
      margin-left: auto;
      margin-right: auto;
      line-height: 1.9em;
    }
   
    img.clip-animation  {
  
      display: block;
      margin-left: 15px; 
      width: 200px;    
    }
  }

  @media (min-width: 768px) {
    h1,
    .h1 {
      font-size: 2.5rem;
    }
    h2,
    .h2 {
      font-size: 2rem;
    }
    h3,
    .h3 {
      font-size: 1.5rem;
    }
    h4,
    .h4 {
      font-size: 1.25rem;
    }
    p {
      font-size: 1.1rem;
    }
  }

  @media (min-width: 768px) {
    footer {
      text-align: left;
    }
    .footer {
      display: flex;
      justify-content: space-between;
    }
    .footer ul li {
      display: inline-block;
    }
    .footer ul li::after {
      content: "â€¢";
      padding-right: 8px;
      padding-left: 8px;
    }
    .footer ul li:last-child::after {
      content: "";
      padding-right: 0;
      padding-left: 0;
    }
  
    img.clip-animation  {
  
      display: block;
      margin-left: 15px; 
      width: 200px;    
    }
  }